<template>
  <div class="overflow-x-auto py-2 sm:px-6 lg:px-2 rounded-md">
    <table class="w-full">
      <slot />
      <tbody>
        <tr v-for="(row, index) in dataTable" :key="index" class="hover:shadow-lg hover:-translate-y-1 hover:-translate-x-1 transition-all z-9" :class="index % 2 === 0 ? 'bg-gray-100' : 'bg-white'">
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['name'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['role'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['phone'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['rg'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['cpf'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['address'] }}
              </span>
            </div>
          </td>
          <td class="text-left whitespace-nowrap pr-3 text-sm text-gray-900 sm:pl-0 cursor-pointer min-w-[10px]">
            <div :class="{ 'text-center': center }">
              <span class="pl-2">
                {{ row['number'] }}
              </span>
            </div>
          </td>
          <td
            class="flex justify-center whitespace-nowrap py-1 pr-2 text-sm font-medium text-gray-900 sm:pl-0"
          >
            <ActionsButtonVue
              :link="url.edit + dataTable[index].id"
              @getReport="generateReport(dataTable[index].id)"
              @delete="deleteEmployee(dataTable[index].id)"
            />
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { storeApp } from '@/store'
import ActionsButtonVue from './../../../components/Tables/ActionsButton.vue'
import EmployeesService from '../services/employees.service'
import { useToast } from 'vue-toastification'

export default {
  name: 'EmployeesTable',
  components: {
    ActionsButtonVue
  },
  props: {
    dataTable: [Object, Array],
    center: { Boolean, default: false }
  },
  data () {
    return {
      url: {
        edit: '/employees/edit/',
        delete: '/employees/delete/'
      },
      columns: this.dataTable,
      toast: useToast()
    }
  },
  methods: {
    async generateReport (id) {
      storeApp.state.loading = true
      await EmployeesService.generateEmployeeReport(id)
        .then(res => {
          storeApp.state.loading = false
          window.location.href = encodeURI(res.data.temp)
        }).catch(err => {
          storeApp.state.loading = false
          this.toast('Algo deu errado ao emitir relatrio', { toastClassName: 'toast-error' })
        })
    },
    deleteEmployee (id) {
      storeApp.state.loading = true
      const confirm = window.confirm('Deseja realmente desativar esse funcionrio ?')
      if (confirm) {
        EmployeesService.deleteEmployee(id).then(res => {
          if (res.data.success) { this.toast('Funcionrio desativado com sucesso!', { toastClassName: 'toast-success' }) }
          window.location.reload()
        }).catch(err => {
          this.toast('Erro ao excluir item!', { toastClassName: 'toast-error' })
        })
      }
    }
  }
}
</script>
